// src/UpdateConfigComponent.js
import React, { useState } from 'react';
import axios from 'axios';
import { Link } from 'react-router-dom';
import { Button, Slider } from 'antd';
import { Image } from 'antd';
import { LikeTwoTone, DislikeTwoTone} from '@ant-design/icons';

function UpdateConfigComponent() {
    axios.defaults.baseURL = 'http://localhost:5000'; // 
    const param = 'gen_num'; //  "gen_num"
    const [value, setValue] = useState('');
    const [imageBase64, setImageBase64] = useState(''); // Base64
    const [clicked, setClicked] = useState(false);
    const updateConfig = () => {
        const newConfig = { [param]: value };
        axios.post('/update-config', { new_config: newConfig })
            .then(response => {
                console.log(response.data);
                // Base64
                if (response.data.image_base64) {
                    setImageBase64(response.data.image_base64);
                }
            })
            .catch(error => {
                console.error('Error updating config:', error);
            });
    };
    const handleClick = () => {
        // HTTPFlask
        axios.post('/copy-file')
          .then(response => {
            // 
            setClicked(true);
            console.log('Add image successful')
          })
          .catch(error => {
            // 
            console.error('Error copying file:', error);
          });
      };

    // 
    // const valueOptions = [
    //     { value: '1', label: '1' },
    //     { value: '2', label: '2' },
    //     { value: '3', label: '3' },
    //     // 
    // ];

    return (
        // <div>
        //     <h2></h2>
        //     {/* param "gen_num" */}
        //     <p></p>
        //     {/* value */}
        //     {/* <select value={value} onChange={e => setValue(e.target.value)}>
        //         <option value=""></option>
        //         {valueOptions.map(option => (
        //             <option key={option.value} value={option.value}>
        //                 {option.label}
        //             </option>
        //         ))}
        //     </select> */}
        //      <Slider
        //         min={1} // 
        //         max={10} // 
        //         step={1} // 
        //         value={value} // 
        //         onChange={setValue} // 
        //         style={{ width: '20%' }}
        //     />
        //     <Button type="primary" onClick={updateConfig}></Button>

        //     {/*  */}
        //     {imageBase64 && (
        //         <div>
        //             <h3></h3>
        //             <Image width={200} src={`data:image/png;base64,${imageBase64}`} alt="Generated" />
        //         </div>
        //     )}

        //     {/* React RouterLinkHistoryImages */}
        //     <div>
        //         <Link to="/history">
        //         <Button type="primary"></Button>
        //         </Link>
        //     </div>
        // </div>
        <div style={{ display: 'flex' }}>
            <div style={{ flex: 0.5 }}>
                <h2></h2>
                <div style={{ width: 300, height: 300, border: '5px dashed #ccc', display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
                    {imageBase64 ? (
                        <Image width={285} src={`data:image/png;base64,${imageBase64}`} alt="Generated" />
                    ) : (
                        <p></p>
                    )}
                </div>

                <LikeTwoTone onClick={handleClick}  style={{  fontSize: '32px' }}/><DislikeTwoTone style={{fontSize: '32px'}}/>
            </div>
            <div style={{ borderRight: '1px solid #ccc' }}></div> {/*  */}
            <div style={{ flex: 1, padding: '0 20px' }}>
                <h2></h2>
                <Slider
                    min={1}
                    max={10}
                    step={1}
                    value={value}
                    onChange={setValue}
                    style={{ width: '80%' }}
                />
                <Button type="primary" onClick={updateConfig}></Button>
                <div style={{ flex: 1,padding: '60px 0px' }}>
                {/* <Link to="/history">
                    <Button type="primary"></Button>
                </Link> */}
            </div>
            </div>
           
        </div>
    );
}

export default UpdateConfigComponent;
